@tailwind base;
@tailwind components;
@tailwind utilities;


html, body {
  height: 100%;
  margin: 0;
  font-family: "Arial", sans-serif;
  scroll-behavior: smooth;
}

main {
  scroll-snap-type: y mandatory;
} 
h1, p {
  text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

section {
  height: 100vh;
  scroll-snap-align: start;
  display: block;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
canvas {
  display: block;
}
button:focus {
  outline: none;
}
.snap-start {
  scroll-snap-align: start; /* Align each section at the top during scrolling */
}

.bg-stars-pattern {
  background: url("https://www.transparenttextures.com/patterns/stardust.png");
  background-size: cover;
}

.bg-clouds-pattern {
  background: url("../public/assets/images/cloud.png");
  background-size: cover;
}
.bg-mountains-pattern {
  background: url("../public/assets/svgs/mountain.png");
  background-size: cover;
}

.bg-path-pattern {
  background: url("../public/assets/svgs/path-background.png");
  background-size: contain;
  background-repeat: no-repeat;
}

.bg-education-gradient {
  background: linear-gradient(135deg, rgba(29, 78, 216, 0.2), rgba(190, 24, 93, 0.2));
  backdrop-filter: blur(10px);
}

.perspective {
  perspective: 1000px;
}
.tooltip {
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.tooltip:hover {
  opacity: 1;
}

.bg-grid-pattern {
  background-image: radial-gradient(circle, #00f 1px, transparent 1px);
  background-size: contain;
  background-repeat: no-repeat;
}

/* Hide the scrollbar */
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

.scrollbar-hide {
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
}

